.newsletter {
  & input {
    width: 150px;
    border: none;
  }

  button {
    height: 41px;
    width: 100px;
    margin-left: -4px;
    border: none;

    background: rgb(230, 132, 10);
    color: white;
    font-size: 1rem;
    text-transform: uppercase;
    cursor: pointer;
  }
}

.input-text-email {
  float: left;
  height: 30px;
  width: 200px;
  font-size: 1rem;
  font-weight: $thin;
  letter-spacing: 1px;
  padding: 20px;
  border: gray 1px solid;
  font-size: 0.85rem;

  &:focus {
    background-color: yellow;
    color: blue;
    border: 0px; // removes border on selction, it can be customized
    outline: none; // removes outline on selection
  }
}

.input-text-area {
  width: 100%;
  font-family: $font-family-primary;
  float: left;
  font-size: 1rem;
  font-weight: $thin;
  letter-spacing: 1px;
  border: gray 1px solid;
  padding: 20px;
  font-size: 0.85rem;
  outline: none; // removes outline and resize corner
  resize: none; // removes outline and resize corner
}

.select-css {
  display: block;
  float: left;
  width: 100%;
  max-width: 100%;
  font-size: 1rem;
  font-weight: $thin;
  letter-spacing: 1px;
  padding: 10px;
  border: gray 1px solid;
  font-size: 0.85rem;
  // -moz-appearance: none;
  // -webkit-appearance: none;
  // line-height: 1.3;
  outline: none;

  &::-ms-expand {
    display: none;
  }
  &:hover {
    border-color: #888;
  }
  &:focus {
    border-color: #aaa;
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, 0.7);
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222;
    outline: none;
  }
  & option {
    font-weight: normal;
  }
}

.select-css1 {
  font-size: 16px;
  font-family: sans-serif;
  font-weight: 700;
  color: #444;
  padding: 0.6em 1.4em 0.5em 0.8em;

  box-sizing: border-box;
  margin: 0;
  border-radius: 0.5em;

  appearance: none;
  background-color: #fff;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),
    linear-gradient(to bottom, #ffffff 0%, #e5e5e5 100%);
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}
.select-css::-ms-expand {
  display: none;
}
.select-css:hover {
  border-color: #888;
}
.select-css:focus {
  border-color: #aaa;
  box-shadow: 0 0 1px 3px rgba(59, 153, 252, 0.7);
  box-shadow: 0 0 0 3px -moz-mac-focusring;
  color: #222;
  outline: none;
}
.select-css option {
  font-weight: normal;
}
